/* Toast */

.jq-toast-wrap {
    .jq-icon-success {
        background-color: $success;
    }
    .jq-icon-info {
        background-color: map-get($theme-colors, 'info');
    }
    .jq-icon-warning {
        background-color: $warning;
    }
    .jq-icon-error {
        background-color: $danger;
    }
}